.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


a:link {
  color:white;
  background-color: transparent;
  text-decoration: none;
}

a:visited {
  color:white;
  background-color: transparent;
  text-decoration: none;
}
.jumbotron{
  background-color:black
}

.btn{

  box-shadow: none;
  outline:none;
  border:none;
}

.dropdown-toggle:focus{
  box-shadow: none;
  outline:none;
  border:none; 


}
.dropdown-toggle{
  box-shadow: none;
  outline:none;
  border:none;

}
.btn-white{
  box-shadow: none;
  outline:none;
  border:none;

}
.btn-black{
  box-shadow: none;
  outline:none;
  border:none;
  background-color: black;

}

.btn:hover{
  background-color:black;
  outline:none;
  border:none;
}
.invisible{
  color:black;

}
.visible{
  color:white
}

.footer{
  background-color: black;
  position:fixed;
  bottom:0px;
  color:grey;
  width:100%;
  text-align:center;

}